import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/components/HomePage/HomePage'
import Main from '@/components/Main'
import page1 from '@/components/page1/page1'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Main',
      components: {
        default: Main
      },
      children: [
        {
          name: HomePage,
          path: '',
          component: HomePage
        },
        {
          name: page1,
          path: 'page1',
          component: page1
        }
      ]
    }
  ]
})



